<template>
  <div class="history-score">
    <!--提示区域-->
    <div class="tip-area" v-show="showTip">
      <!--图标区域-->
      <span class="icon-star"></span>
      <!--提示文本-->
      <span class="tip-title">点击图表中的圆点，可切换历次考试</span>
      <!--关闭按钮-->
      <span class="icon-close-btn" @click="closeTip"></span>
    </div>
    <div id="container"></div>
    <div class="score-area">
      <p>
        <span>满分 <em style="color: #000; font-style: normal;">{{totalScore}}</em></span>
        <span>班级平均分 <em style="color: #000; font-style: normal;">{{avg}}</em></span>
        <span>最高分 <em style="color: #000; font-style: normal;">{{max}}</em></span>
        <span>我的分数 <em style="color: #ce5961; font-style: normal;">{{myScore}}</em></span>
      </p>
    </div>
  </div>
</template>

<script>
import api from '../utils/api';
const echarts = require('echarts/lib/echarts');
// 引入柱状图
require('echarts/lib/chart/line');
// 引入提示框和标题组件
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');

export default {
  name: 'history-score',
  data () {
    return {
      showTip: true,
      datalist: [],
      chart: '',
      maxData: [],
      avgData: [],
      myData: [],
      max: '',
      avg: '',
      myScore: '',
      totalScore: '',
      option: {
        title: {
          text: ''
        },
        tooltip: {
          padding: [16, 20, 16, 20],
          trigger: 'item',
          formatter: function (params) {
            let curValue = localStorage.getItem('curValue');
            let res = '<span>' + curValue + '</span>';
            return res;
          },
          triggerOn: 'click',
          textStyle: {
            fontSize: 24,
            color: '#fff'
          },
          backgroundColor: '#383838'
        },
        color: ['#6ED7CB', '#FFC959', '#FD8683'], // 手动设置每个图例的颜色
        legend: {
          bottom: '0',
          itemWidth: 24, // 图例标记的图形宽度
          itemHeight: 24, // 图例标记的图形高度
          textStyle: {
            color: '#606060',
            fontSize: 26
          },
          itemGap: 76,
          data: ['班级最高分', '班级平均分', '我的分数']
        },
        grid: {
          left: '0%',
          right: '5%',
          bottom: '10%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: '#dddee3'
              }
            },
            axisLabel: {
              margin: 20,
              textStyle: {
                color: '#606060',
                fontSize: 26,
                fontFamily: 'Microsoft YaHei'
              },
              interval: 0
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false,
              alignWithLabel: true
            },
            data: []
          }
        ],
        yAxis: [
          {
            type: 'value',
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: '#dddee3'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#dddee3',
                type: 'dashed'
              }
            },
            axisLabel: {
              textStyle: {
                color: '#606060',
                fontSize: 26,
                fontFamily: 'Microsoft YaHei'
              }
            }
          }
        ],
        series: [
          {
            name: '班级最高分',
            type: 'line',
            id: 'max',
            symbol: 'circle',
            symbolSize: 40,
            areaStyle: {
              normal: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [{
                    offset: 0, color: '#DEFFFC' // 0% 处的颜色
                  }, {
                    offset: 1, color: '#fff' // 100% 处的颜色
                  }],
                  globalCoord: false // 缺省为 false
                }}
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: '#6ED7CB',
                  width: 2
                }
              }
            },
            data: []
          },
          {
            name: '班级平均分',
            type: 'line',
            id: 'avg',
            symbol: 'circle',
            symbolSize: 40,
            areaStyle: {
              normal: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [{
                    offset: 0, color: '#FFF8E8' // 0% 处的颜色
                  }, {
                    offset: 1, color: '#fff' // 100% 处的颜色
                  }],
                  globalCoord: false // 缺省为 false
                }
              }
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: '#FFC959',
                  width: 2
                }
              }
            },
            data: []
          },
          {
            name: '我的分数',
            type: 'line',
            id: 'my',
            symbol: 'circle',
            symbolSize: 40,
            areaStyle: {
              normal: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [{
                    offset: 0, color: '#FFE1E1' // 0% 处的颜色
                  }, {
                    offset: 1, color: '#fff' // 100% 处的颜色
                  }],
                  globalCoord: false // 缺省为 false
                }
              }
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  color: '#FD8683',
                  width: 2
                }
              }
            },
            data: []
          }
        ]
      }
    };
  },
  created () {
    this.getHistoryScore();
  },
  mounted () {
  },
  methods: {
    getHistoryScore () {
      let params = {};
      api.getHistoryScore(params).then((res) => {
        if (res && res.data && res.data.data) {
          const data = res.data;
          this.datalist = data.data;
          this.initData();
          // console.log(this.datalist);
        }
      }, (err) => {
        console.log(err);
      });
    },
    chartItem () {
      this.chart = echarts.init(document.querySelector('#container'));
      this.chart.setOption(this.option);
      this.chart.on('click', (params) => {
        // console.log(params);
        let index = params.dataIndex;
        let curData = this.datalist[index];
        this.max = curData.max_score;
        this.avg = curData.avg_score;
        this.myScore = curData.myScore;
        this.totalScore = curData.total_score;
        let curValue = curData.exam.exName;
        localStorage.setItem('curValue', curValue);
      });
    },
    initData () {
      if (this.datalist && Array.isArray(this.datalist) && this.datalist.length > 0) {
        // 页面初始化 展示班级平均分，最高分，我的分数，总分
        let curExamId = this.$route.query.examId;
        [].forEach.call(this.datalist, (data) => {
          this.maxData.push(data.max_score);
          this.avgData.push(data.avg_score);
          this.myData.push(data.myScore);
          this.option.xAxis[0].data.push((data && data.exam && this.filterTime(data.exam.createTime)) || '');
          // console.log(data.exam.exId);
          // 判断传过来的考试id是否在当前数组历史成绩中，如果有默认展示成绩信息
          if (parseInt(curExamId, 10) === data.exam.exId) {
            this.max = data.max_score;
            this.avg = data.avg_score;
            this.myScore = data.myScore;
            this.totalScore = data.total_score;
          }
        });
        [].forEach.call(this.option.series, (data) => {
          switch (data.id) {
            case 'max':
              data.data = this.maxData;
              break;
            case 'avg':
              data.data = this.avgData;
              break;
            case 'my':
              data.data = this.myData;
              break;
            default:
              break;
          }
        });
        // console.log(this.maxData, this.avgData, this.myData);
        console.log(this.option);
        this.chartItem();
      }
    },
    filterTime (value) {
      let time = new Date(value - 0);
      let month = (time.getMonth() + 1) > 9 ? (time.getMonth() + 1) : '0' + (time.getMonth() + 1);
      let date = time.getDate() > 9 ? time.getDate() : '0' + time.getDate();
      return month + '.' + date;
    },
    closeTip () {
      this.showTip = false;
    }
  }
};
</script>
<style rel="stylesheet/scss" lang="scss">
  html, body {
    width: 100%;
    height: 100%;
  }

  .history-score {
    width: 100%;
    height: 100%;
    /*background: #f2f2f4;*/
    overflow-x: hidden;
    overflow-y: auto;
    padding: 26px 20px 0 20px;
    background: #fff;
    .score-area {
      display: flex;
      margin-top: 30px;
      width: 333px;
      height: 84px;
      padding: 20px;
      background: #F9F9F9;
      border-radius: 2px;
      p:nth-child(1) {
        width: 100%;

      }
      span {
        margin-bottom: 16px;
        display: inline-block;
        width: 48%;
        font-size: 15px;
        color: #606060;
        line-height: 14px;
      }
    }
    /*tip区域*/
    .tip-area{
      position: relative;
      padding-left: 10px;
      width: 100%;
      height: 46px;
      background: #FEF1DB;
      border-radius: 100px 0 125px 100px;
      .icon-star{
        float: left;
        width: 54px;
        height: 100%;
        background: url("../assets/icon-star.png") center no-repeat;
      }
      .tip-title{
        float: left;
        margin-left: 6px;
        height: 46px;
        line-height: 46px;
        font-size: 14px;
        color: #E7440B;
      }
      .icon-close-btn{
        position: absolute;
        top: 0;
        right: 0;
        width: 22px;
        height: 22px;
        background: url("../assets/icon-close.png") center no-repeat;
      }
    }
  }

  #container {
    margin-top: 10px;
    width: 100%;
    height: 260px;
  }
</style>



